import { GlowFilter } from '@pixi/filter-glow';
import * as PIXI from 'pixi.js'
import { objectBase } from '../engine/objectHandlers/objectBase';
import { objectFunctions } from '../engine/objectHandlers/objectFunctions';
import { roomEvent } from '../engine/roomEvent/roomEvent';
import { ticker } from '../engine/ticker';
import { scene_home } from '../scenes/scene_home';


export class textPromptBox extends objectBase{
    switch: boolean = false;
    static objectName = "textPromptBox";
    friction = 0.0;

    private textContainer: PIXI.Text | null = null;

    private blurrCharacters = 10;
    private blurredCharactersArray: PIXI.Text[] = [];
    private blurredCharactersFilterArray: any/*BlurFilter*/[] = [];
    
    
    private okButton: PIXI.Text | null = null;
    private okButtonGlow: GlowFilter | null = null
    private okButtonGlowStrength = 15;

    private promptWidth = 0;
    private promptHeight = 0;
    private FontStyle: PIXI.TextStyle | null = null;
    private textPrompts: string[] = [];
    private currentPromptLines: string[] = [];
    private currentPrompt = "";
    private currentPromptIndex = 0;
    private displayedLengthOfPrompt = 0;

    private characterSpeed = 3;
    private characterSpeedCount = 10;

    
    private fontSize = 29;

    private boxPadding = 8;
    private promptDone = false;
    private promptAlpha = 1;

    private enterKeyHeld = false;

    private promptDoneCallback: ((l: objectFunctions)=>void) = ()=>{};

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, textPromptBox.objectName);
        super.setCollision(0, 32, 64, 64);

        console.log("New text prompt: ",input);
        try{
            this.textPrompts = JSON.parse(input);
        }catch(err){
            console.log("promt could not parse: ",input);
        }
        
        //console.log(this.textPrompts);
        this.currentPrompt = this.textPrompts[0];
        

        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0x003eff);
            newGraphics.drawRect(0, 32, 64, 64);
            newGraphics.endFill();
            g.addChild(newGraphics);

            return g;
        });

        
    }

    afterInit(l: objectFunctions){
        this.okButtonGlow = new GlowFilter({ distance: 15, outerStrength: 0, color: 0xfffec1 });
        this.promptWidth = l.getWindowWidth()*0.75;
        this.promptHeight = l.getWindowHeight()*0.25;

        this.g.removeChildren();
        let newGraphics = new PIXI.Graphics();
        newGraphics.beginFill(0x000000, 0.4);
        newGraphics.drawRect(-this.boxPadding, -this.boxPadding, this.promptWidth+this.boxPadding*2, this.promptHeight+this.boxPadding*2);
        newGraphics.endFill();
        this.g.addChild(newGraphics);

        this.FontStyle = new PIXI.TextStyle({
            breakWords: false,
            /*dropShadowAlpha: 0.5,
            dropShadowAngle: 0.6,*/
            //dropShadowColor: "#46382b",
            fill: "white",
            fontFamily: "Inconsolata_ExtraCondensed-Light",
            fontSize: this.fontSize,
            lineHeight: 24,
            //lineJoin: "bevel",
            wordWrap: true,
            letterSpacing: 3,
            wordWrapWidth: this.promptWidth
        });

        this.currentPromptLines = PIXI.TextMetrics.measureText(this.currentPrompt, this.FontStyle!).lines;
        this.currentPrompt = "";
        this.currentPromptLines.forEach(line => {
            //console.log("line: ",line);
            this.currentPrompt += line.replace(/ /g, '_') + "\n";
        });
        

        for(let i=0; i<this.blurrCharacters; i++){
            let newText = new PIXI.Text("", this.FontStyle);
            let newFilter = new PIXI.filters.BlurFilter(6, 3 + 2*((this.blurrCharacters-i)/this.blurrCharacters));
            newText.filters = [newFilter]
            this.blurredCharactersArray.push(newText);
            this.blurredCharactersFilterArray.push(newFilter);

            this.g.addChild(newText);
        }

        this.textContainer = new PIXI.Text("", this.FontStyle);

        this.g.addChild(this.textContainer);

        this.okButton = new PIXI.Text("", this.FontStyle);
        let widthOfOk = PIXI.TextMetrics.measureText("O.K.", this.FontStyle!).lineWidths[0];
        this.okButton.x = (this.promptWidth/2)-(widthOfOk/2);
        this.okButton.y = this.promptHeight-40;
        this.okButton.filters = [this.okButtonGlow];
        this.g.addChild(this.okButton);
        
    }

    public setPromptCallback(callback: ((l: objectFunctions)=>void)){
        this.promptDoneCallback = callback;
    }

    logic(l: objectFunctions){
        this.g.x = l.getCameraX()-(this.promptWidth/2);
        this.g.y = l.getCameraY() + (l.getWindowHeight()/2) - (this.promptHeight) - 48;


        this.okButtonGlowStrength = Math.cos(ticker.getTicks()*0.074)*1.4;
        this.okButtonGlow!.outerStrength = this.okButtonGlowStrength;

        if(this.promptDone){
            this.g.alpha = this.promptAlpha;
            this.promptAlpha *= 0.928;
            if(this.promptAlpha < 0.004){
                console.log("Callback: ",this.promptDoneCallback)
                //l.goToRoom(scene_home);
                this.promptDoneCallback(l);
                l.deleteObject(this);
            }
            return;
        }

        if(this.enterKeyHeld){
            if(this.displayedLengthOfPrompt < this.currentPrompt.length-9 + this.blurrCharacters){
                this.displayedLengthOfPrompt += 10;
            }
        }
        if(l.checkKeyPressed("Enter")){
            this.enterKeyHeld = true;
            if(this.displayedLengthOfPrompt >= this.currentPrompt.length + this.blurrCharacters){
                this.textContainer!.text = "";
                if(this.currentPromptIndex < this.textPrompts.length-1){
                    this.currentPromptIndex++;
                    this.currentPrompt = this.textPrompts[this.currentPromptIndex];
                    this.currentPromptLines = PIXI.TextMetrics.measureText(this.currentPrompt, this.FontStyle!).lines;
                    this.currentPrompt = "";
                    this.currentPromptLines.forEach(line => {
                        //console.log("line: ",line);
                        this.currentPrompt += line.replace(/ /g, '_') + "\n";
                    });
                    this.displayedLengthOfPrompt = 0;
                    this.okButton!.text = "";
                    this.enterKeyHeld = false;
                }else{
                    this.promptDone = true;
                }
            }
        }

        if(l.checkKeyReleased("Enter")){
            this.enterKeyHeld = false;
        }

        let maxBlur = 20;
        let bluePerIndex = maxBlur/this.blurrCharacters;
        if(this.characterSpeedCount > 0){
            this.characterSpeedCount--;
            
            for(let i=0; i<this.blurrCharacters; i++){
                this.blurredCharactersFilterArray[i].blur = (maxBlur-bluePerIndex-(i*bluePerIndex))
                                        +(this.characterSpeedCount/this.characterSpeed)*bluePerIndex;
            }
        }else{
            this.characterSpeedCount = this.characterSpeed;
            let extraSpace = 0;
            let lastLineWidth = 0;
            let lastLineWithSpaces = 0;

            if(this.displayedLengthOfPrompt-this.blurrCharacters+1 > 0){
                this.textContainer!.text = this.currentPrompt.substr(0, this.displayedLengthOfPrompt-this.blurrCharacters+1).replace(/_/g, ' ');
            }
            
            for(let i=0; i<this.blurrCharacters; i++){
                if(this.displayedLengthOfPrompt-i < this.currentPrompt.length){
                    let targetChar: string | undefined = this.currentPrompt[this.displayedLengthOfPrompt-i];
                    if(targetChar == undefined){
                        this.blurredCharactersArray[i].text = "";
                    }else{
                        if(targetChar == "_"){
                            this.blurredCharactersArray[i].text = " ";
                        }else{
                            this.blurredCharactersArray[i].text = targetChar;
                        }
                    }
                    
                    
                    let lines = PIXI.TextMetrics.measureText(this.currentPrompt.substr(0, this.displayedLengthOfPrompt-i), this.FontStyle!).lines;
                    let textMetricsNoSpaces = PIXI.TextMetrics.measureText(this.currentPrompt.replace(/ /g, '_').substr(0, this.displayedLengthOfPrompt-i), this.FontStyle!);
                    let textMetricsWithSpaces = PIXI.TextMetrics.measureText(this.currentPrompt.substr(0, this.displayedLengthOfPrompt-i), this.FontStyle!);
                    lastLineWidth = textMetricsNoSpaces.lineWidths[textMetricsNoSpaces.lineWidths.length-1]+extraSpace+4;
                    lastLineWithSpaces = textMetricsWithSpaces.lineWidths[textMetricsWithSpaces.lineWidths.length-1]+extraSpace+4;
                    let latestLine = lines[lines.length-1];
                    //Check if last char should be a space
                    if(this.currentPrompt[lines[lines.length-1].length] == " "){
                        latestLine += " ";
                    }
                    let lineWidths = PIXI.TextMetrics.measureText(latestLine.replace(/ /g, '_'), this.FontStyle!).lineWidths;
                    let xPosition = lineWidths[lineWidths.length-1];
                    

                    this.blurredCharactersArray[i].x = xPosition + 4;// - (textMetricsWithSpaces.lines.length-1)*lastLineWithSpaces;
                    this.blurredCharactersArray[i].y = textMetricsWithSpaces.lineHeight*(textMetricsWithSpaces.lineWidths.length-1);

                    this.blurredCharactersFilterArray[i].blur = (maxBlur-bluePerIndex-(i*bluePerIndex))
                                        +(this.characterSpeedCount/this.characterSpeed)*bluePerIndex;
                    //l.getRenderer().render(this.g);
                }else{
                    this.blurredCharactersArray[i].text = "";
                }
            }
            


            
            
            
            if(this.displayedLengthOfPrompt < this.currentPrompt.length + this.blurrCharacters){
                this.displayedLengthOfPrompt ++;  
            }else{
                this.okButton!.text = "O.K.";
            }
            
        }
    };



}