import * as PIXI from 'pixi.js'
import { animConfig } from '../../../engine/objectHandlers/animConfig';
import { objectBase } from '../../../engine/objectHandlers/objectBase';
import { objectFunctions } from '../../../engine/objectHandlers/objectFunctions';
import { roomEvent } from '../../../engine/roomEvent/roomEvent';


export class textPrompt extends objectBase{
    switch: boolean = false;
    static objectName = "textPrompt";
    friction = 0.0;

    private text = "[SPACE]";

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, textPrompt.objectName);
        super.setCollision(0, 32, 64, 64);

        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0x003eff);
            newGraphics.drawRect(0, 32, 64, 64);
            newGraphics.endFill();
            g.addChild(newGraphics);

            let text = new PIXI.Text('[SPACE]',{fontFamily : 'Arial', fontSize: 24, fill : 0xff1010, align : 'center'});

            g.addChild(text);

            g.calculateBounds();

            return g;
        });
    }

    logic(l: objectFunctions){
        
    };



}