import * as PIXI from 'pixi.js'
import { objectBase } from '../../engine/objectHandlers/objectBase';
import { objectFunctions } from '../../engine/objectHandlers/objectFunctions';
import { roomEvent } from '../../engine/roomEvent/roomEvent';
import { roomIndex } from '../../scenes/scene_index';
import { player } from '../player';


export class roomChanger extends objectBase{
    switch: boolean = false;
    static objectName = "roomChanger";
    friction = 0.0;
    inputTemplate = '{"to": "", "from": "", "facing": ""}';
    public targetRoom = "";

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, roomChanger.objectName);
        super.setCollision(0, 0, 32, 32);

        this.targetRoom = input;
        this.outputString = this.targetRoom;

        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0x003eff);
            newGraphics.drawRect(0, 0, 32, 32);
            newGraphics.endFill();
            g.addChild(newGraphics);

            g.calculateBounds();

            return g;
        });
    }

    logic(l: objectFunctions){
        let collisionTarget = l.isCollidingWith(this, this.collisionBox, [player.objectName]);
        if(collisionTarget != null){
            let inputJson = JSON.parse(this.targetRoom);
            //console.log(inputJson);
            //console.log("Load room: ",roomIndex[inputJson.to]);
            l.loadRoom(roomIndex[inputJson.to], ["target_room",this.targetRoom]);
        }
    };



}