import { AdjustmentFilter } from '@pixi/filter-adjustment';
import * as PIXI from 'pixi.js'
import { iVector } from "../../engine/dataObjects/vector/iVector";
import { nullVector } from "../../engine/dataObjects/vector/nullVector";
import { objectBase } from "../../engine/objectHandlers/objectBase";
import { objectFunctions } from '../../engine/objectHandlers/objectFunctions';
import { roomEvent } from '../../engine/roomEvent/roomEvent';

export class skyBackground extends objectBase{
    static objectName = "skyBackground";
    private skyFilter = `
    precision lowp float;
    varying vec2 vTextureCoord;

    const vec2 star = vec2(0.5, 0.5);

    uniform int timeOfDay;
    uniform float starBrightness;

    float randFromVec(vec2 co){
        return fract(sin(dot(co, vec2(12.9898, 78.233))) * 43758.5453);
    }

    void main(void)
    {

        //Dawn 07:17  -  0
        vec3 dawnColor1 = vec3(0.36784,0.33843,0.5545);
        vec3 dawnColor2 = vec3(0.78117,0.592,0.5447);


        //Sunrise 08:04  -  1
        vec3 sunriseColor1 = vec3(0.4219,0.6047,0.8945);
        vec3 sunriseColor2 = vec3(0.8649,0.86392,0.54235);

        //Morning 10:00  -  2
        vec3 morningColor1 = vec3(0.45019,0.6929,0.97804);
        vec3 morningColor2 = vec3(0.6494,0.82745,0.98745);

        //Afternoon 14:00  -  3
        vec3 afternoonColor1 = vec3(0.3145,0.563529,0.8643);
        vec3 afternoonColor2 = vec3(0.56588,0.770196,0.92941);

        //Sunset 17:00  -  4
        vec3 sunsetColor1 = vec3(0.5309,0.378,0.53098);
        vec3 sunsetColor2 = vec3(0.92588,0.66549,0.238039);

        //Dusk 17:54  -  5
        vec3 duskColor1 = vec3(0.2549,0.29568,0.5545);
        vec3 duskColor2 = vec3(0.8827,0.5494,0.2988);

        //Night 19:00  -  6
        vec3 nightColor1 = vec3(0.0023137,0.061568,0.138823);
        vec3 nightColor2 = vec3(0.14667,0.28783,0.4847);


        float nightSkyHeight = 0.5;
        vec3 color = mix(dawnColor1,dawnColor2, vTextureCoord.y);

        if(timeOfDay == 1){
            color = mix(sunriseColor1,sunriseColor2, vTextureCoord.y);
        }else if(timeOfDay == 2){
            color = mix(morningColor1,morningColor2, vTextureCoord.y);
        }else if(timeOfDay == 3){
            color = mix(afternoonColor1,afternoonColor2, vTextureCoord.y);
        }else if(timeOfDay == 4){
            color = mix(sunsetColor1,sunsetColor2, vTextureCoord.y);
        }else if(timeOfDay == 5){
            color = mix(duskColor1,duskColor2, vTextureCoord.y);
        }else if(timeOfDay == 6){
            color = mix(nightColor1,nightColor2, vTextureCoord.y);
        }

        float randNum = randFromVec(vTextureCoord);

        if(vTextureCoord.y < 0.5){
            float closeToTopOfStarPart = (vTextureCoord.y/0.5) * 1.4;
            if(randNum < 0.001 * starBrightness){
                gl_FragColor = mix(vec4(1.0, 1.0, 1.0, 1.0), vec4(color,1.0), closeToTopOfStarPart);
            }else{
                gl_FragColor = vec4(color,1.0);
            }
        }else{
            gl_FragColor = vec4(color,1.0);
        }
        
        
    }`;

    private skyUniform = {
        'timeOfDay': 0,
        'starBrightness': 1
    };
    private timeOfDay = 2;

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, skyBackground.objectName);

        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();
            newGraphics.beginFill(0x000000, 1); // use an alpha value of 1 to make it visible
            newGraphics.drawRect(-1280, -360, 1280*2, 720);
            newGraphics.endFill();
            g.addChild(newGraphics);
            g.cacheAsBitmap = true;
            return g;
        });
    }

    afterInit(roomEvents: objectFunctions){
        if(roomEvents.flags().has("dawn")){
            this.timeOfDay = 0;  
        }else if(roomEvents.flags().has("sunrise")){
            this.timeOfDay = 1;  
        }else if(roomEvents.flags().has("morning")){
            this.timeOfDay = 2;  
        }else if(roomEvents.flags().has("afternoon")){
            this.timeOfDay = 3;  
        }else if(roomEvents.flags().has("sunset")){
            this.timeOfDay = 4;  
        }else if(roomEvents.flags().has("dusk")){
            this.timeOfDay = 5;  
        }else if(roomEvents.flags().has("night")){
            this.timeOfDay = 6;  
        }
        
        this.skyUniform.timeOfDay = this.timeOfDay;
        if(this.timeOfDay == 0){//dawn
            this.skyUniform.starBrightness = 0.5;
        }else if(this.timeOfDay == 1){//Sunrise
            this.skyUniform.starBrightness = 0.4;
        }else if(this.timeOfDay == 2){//Morning
            this.skyUniform.starBrightness = 0.1;
        }else if(this.timeOfDay == 3){//afternoon
            this.skyUniform.starBrightness = 0.1;
        }else if(this.timeOfDay == 4){//Sunset
            this.skyUniform.starBrightness = 0.2;
        }else if(this.timeOfDay == 5){//Dusk
            this.skyUniform.starBrightness = 0.4;
        }else if(this.timeOfDay == 6){//night
            this.skyUniform.starBrightness = 1.0;
        }
        
        var filterSky  = new PIXI.Filter(undefined, this.skyFilter, this.skyUniform);
            filterSky.autoFit = false;
        this.g.filters = [filterSky];
        this.g.cacheAsBitmap = true;
        roomEvents.getRenderer().render(this.g);
        this.g.filters = [];

        if(this.timeOfDay == 0){//dawn
            //vec3(0.36784,0.33843,0.5545);
            //vec3(0.78117,0.592,0.5447);
            roomEvents.setLayerFilterExclude("GUILayer",[new AdjustmentFilter({
                red: 0.57,
                green: 0.48,
                blue: 0.545,
                contrast: 1.0,
                brightness: 1.0,
            })]);
        }else if(this.timeOfDay == 1){//Sunrise
            /*vec3 sunriseColor1 = vec3(0.4219,0.6047,0.8945);
            vec3 sunriseColor2 = vec3(0.8649,0.86392,0.54235);*/
            roomEvents.setLayerFilterExclude("GUILayer",[new AdjustmentFilter({
                red: 0.64,
                green: 0.72,
                blue: 0.714,
                contrast: 1.0,
                brightness: 1.0,
            })]);
        }else if(this.timeOfDay == 2){//Morning
            //vec3 morningColor1 = vec3(0.45019,0.6929,0.97804);
            //vec3 morningColor2 = vec3(0.6494,0.82745,0.98745);
            roomEvents.setLayerFilterExclude("GUILayer",[new AdjustmentFilter({
                red: 0.645,
                green: 0.855,
                blue: 0.995,
                contrast: 1.0,
                brightness: 1.0,
            })]);
        }else if(this.timeOfDay == 3){//afternoon
            //vec3 afternoonColor1 = vec3(0.3145,0.563529,0.8643);
            //vec3 afternoonColor2 = vec3(0.56588,0.770196,0.92941);
            roomEvents.setLayerFilterExclude("GUILayer",[new AdjustmentFilter({
                red: 0.535,
                green: 0.765,
                blue: 0.99,
                contrast: 1.0,
                brightness: 1.0,
            })]);
        }else if(this.timeOfDay == 4){//Sunset
            //vec3 sunsetColor1 = vec3(0.5309,0.378,0.53098);
            //vec3 sunsetColor2 = vec3(0.92588,0.66549,0.238039);
            roomEvents.setLayerFilterExclude("GUILayer",[new AdjustmentFilter({
                red: 0.725,
                green: 0.515,
                blue: 0.38,
                contrast: 1.0,
                brightness: 1.0,
            })]);
        }else if(this.timeOfDay == 5){//Dusk
            //vec3 duskColor1 = vec3(0.2549,0.29568,0.5545);
            //vec3 duskColor2 = vec3(0.8827,0.5494,0.2988);
            roomEvents.setLayerFilterExclude("GUILayer",[new AdjustmentFilter({
                red: 0.5688,
                green: 0.415,
                blue: 0.4244,
                contrast: 1.0,
                brightness: 1.0,
            })]);
        }else if(this.timeOfDay == 6){//night
            roomEvents.setLayerFilterExclude("GUILayer",[new AdjustmentFilter({
                red: 0.1,
                green: 0.1,
                blue: 1.00,
                contrast: 1.1,
                brightness: 1.2,
            })]);
        }
        
    }

    logic(l: objectFunctions){
        super.logic(l);
        var panPercentage = (l.getCameraX() - l.getCameraBounds()[0])/l.getCameraBounds()[2];
        this.g.x = l.getCameraX() - (640*panPercentage);
        this.g.y = l.getCameraY();

        
        //console.log("panPercentage: ",panPercentage);
    };



}