import { objectBase } from "../../engine/objectHandlers/objectBase";
import * as PIXI from 'pixi.js'
import { roomEvent } from "../../engine/roomEvent/roomEvent";
import { objectFunctions } from "../../engine/objectHandlers/objectFunctions";

export class wideBlock extends objectBase{
    switch: boolean = false;
    static objectName = "wideBlock";
    friction = 0.986;

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, wideBlock.objectName);
        super.setCollision(0, 0, 640, 64);

        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0x000000); 
            newGraphics.drawRect(0, 0, 640, 64);
            newGraphics.endFill();
            g.addChild(newGraphics);
            return g;
        });
        
        /*setInterval(()=>{
            this.switch = !this.switch;
        }, 700);*/
    }

    logic(l: objectFunctions){
        super.logic(l);
        /*if(this.switch){
            super.setNewForce(l.degreesToRadians(0), 3);
        }else{
            super.setNewForce(l.degreesToRadians(180), 3);
        }*/
    };



}