import * as PIXI from 'pixi.js'
import { objectBase } from '../../engine/objectHandlers/objectBase';
//import * as particles from '@pixi/particle-emitter'
import * as particles from 'pixi-particles'
import { resourcesHand } from '../../engine/preload sources/resourcesHand';
import { roomEvent } from '../../engine/roomEvent/roomEvent';
import { objectFunctions } from '../../engine/objectHandlers/objectFunctions';

export class fallingLeavesParticles extends objectBase{
    static objectName = "fallingLeavesParticles";
    private elapsed = Date.now();
    private emitter;

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, fallingLeavesParticles.objectName);
        super.setCollision(0, 0, 256, 256);

        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();
            newGraphics.beginFill(0x223eFF, 0.1);
            newGraphics.drawRect(0, 0, 256, 256);
            newGraphics.endFill();
            g.addChild(newGraphics);

            g.calculateBounds();

            return g;
        });


        //PIXI.particle
        this.emitter = new particles.Emitter(

            // The PIXI.Container to put the emitter in
            // if using blend modes, it's important to put this
            // on top of a bitmap, and not use the root stage Container
            this.g,
        
            // The collection of particle images to use
            [resourcesHand.getStaticTile("fallingLeaf.png").texture],
        
            // Emitter configuration, edit this to change the look
            // of the emitter
            {
                "alpha": {
                    "start": 1,
                    "end": 0
                },
                "scale": {
                    "start": 0.45,
                    "end": 0.6,
                    "minimumScaleMultiplier": 0.5
                },
                "color": {
                    "start": "#ffffff",
                    "end": "#ffffff"
                },
                "speed": {
                    "start": 50,
                    "end": 100,
                    "minimumSpeedMultiplier": 1.03
                },
                "acceleration": {
                    "x": 2,
                    "y": 0
                },
                "maxSpeed": 0,
                "startRotation": {
                    "min": 50,
                    "max": 70
                },
                "noRotation": false,
                "rotationSpeed": {
                    "min": 0,
                    "max": 140
                },
                "lifetime": {
                    "min": 4,
                    "max": 6
                },
                "blendMode": "normal",
                "ease": [
                    {
                        "s": 0,
                        "cp": 0.379,
                        "e": 0.548
                    },
                    {
                        "s": 0.548,
                        "cp": 0.717,
                        "e": 0.676
                    },
                    {
                        "s": 0.676,
                        "cp": 0.635,
                        "e": 1
                    }
                ],
                "frequency": 0.5,
                "emitterLifetime": -1,
                "maxParticles": 32,
                "pos": {
                    "x": 0,
                    "y": 0
                },
                "addAtBack": false,
                "spawnType": "rect",
                "spawnRect": {
                    "x": 0,
                    "y": 0,
                    "w": 801,
                    "h": 20
                }
            }
        );


        this.emitter.emit = true;

    }

    logic(l: objectFunctions){
        var now = Date.now();

        // The emitter requires the elapsed
        // number of seconds since the last update
        this.emitter.update((now - this.elapsed) * 0.001);
        this.elapsed = now;
    }

}