import { hitbox } from "../../../../engine/hitboxes/hitbox/hitbox";
import { iObject } from "../../../../engine/objectHandlers/iObject";
import { baseAttack } from "../../../../engine/hitboxes/baseAttack/baseAttack";
import { movementDirection } from "../../../../engine/action/attackDirections";
import { dummySandbag } from "../../../../objects/dummySandbag";
import { player } from "../../../../objects/player";



export class threeHitNormal extends baseAttack{

    constructor(creator: iObject, direction: movementDirection, attackTargets: string[]){
        super(creator, direction, attackTargets);

        var returnUserFacing = () => {
            return (creator as player).facingRight;
        };

        var collision = [dummySandbag.objectName];

        this.attackSeries
            .newAction().vector(0, 3).userStill(true)
            .create(() => {return hitbox.new([64, 80], 87, 1, attackTargets, returnUserFacing)}).objOffset([48, -5]).objLife(3)
            .startupWait(3)

            .newAction().vector(0, 3).userStill(true).continueWindow(15).removePrevObjsOnCreate()
            .create(() => {return hitbox.new([64, 80], 40, 5, attackTargets, returnUserFacing)}).objOffset([48, -5]).objLife(5)
            .startupWait(5)

            .newAction().vector(50, 6).userStill(true).continueWindow(19)
            .startupWait(5)
            .endWait(16)

            .newAction().userStill(true).removePrevObjsOnCreate()
            .create(() => {return hitbox.new([80, 80], 40, 5, attackTargets, returnUserFacing)}).objOffset([48, 8]).objLife(12)
            .startupWait(0)
            .endWait(10)
        
    }

}