import * as PIXI from 'pixi.js'
import { calculations } from '../engine/calculations';
import { iVector } from '../engine/dataObjects/vector/iVector';
import { vectorFixedDelta } from '../engine/dataObjects/vector/vectorFixedDelta';
import { objectBase } from '../engine/objectHandlers/objectBase';
import { roomEvent } from '../engine/roomEvent/roomEvent';
import { block } from './blocks/static blocks/block';
import { movingBlockHori } from './blocks/movingBlockHori';
import { movingBlockVert } from './blocks/movingBlockVert';
import { tinyBlock32 } from './blocks/tinyBlock32';
import { wideBlock } from './blocks/wideBlock';
import { block32x64 } from './blocks/static blocks/block32x64';
import { block64x32 } from './blocks/static blocks/block64x32';
import { objectFunctions } from '../engine/objectHandlers/objectFunctions';


export class dummySandbag extends objectBase{
    switch: boolean = false;
    static objectName = "dummySandbag";
    gravity: iVector = new vectorFixedDelta(calculations.degreesToRadians(270), 0);
    friction = 0.78;
    airFriction= 0.9947;
    weight:number =  0.037;

    life: number = 1000;
    collidesWithPolygonGeometry = true;

    sameLayerCollisionOnly = true;

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, dummySandbag.objectName);
        super.setCollision(0, 0, 64, 98);

        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0x0000FF); 
            newGraphics.drawRect(0, 0, 64, 98);
            newGraphics.endFill();
            g.addChild(newGraphics);

            
            return g;
        });

        super.addCollisionTarget("player", block.objectName, block32x64.objectName, block64x32.objectName, movingBlockHori.objectName, movingBlockVert.objectName, 
            tinyBlock32.objectName, wideBlock.objectName);
        
    }

    logic(l: objectFunctions){
        super.logic(l);
    };



}