import { objectBase } from "../engine/objectHandlers/objectBase";
import * as PIXI from 'pixi.js'
import { block } from "./blocks/static blocks/block";
import { movingBlockHori } from "./blocks/movingBlockHori";
import { movingBlockVert } from "./blocks/movingBlockVert";
import { iVector } from "../engine/dataObjects/vector/iVector";
import { vectorFixedDelta } from "../engine/dataObjects/vector/vectorFixedDelta";
import { calculations } from "../engine/calculations";
import { roomEvent } from "../engine/roomEvent/roomEvent";
import { resourcesHand } from "../engine/preload sources/resourcesHand";
import { animConfig } from "../engine/objectHandlers/animConfig";
import { ladder } from "./environment/interactive/ladder";
import { tinyBlock32 } from "./blocks/tinyBlock32";
import { wideBlock } from "./blocks/wideBlock";
import { spriteContainer } from "../engine/dataObjects/spriteContainer";
import { block32x64 } from "./blocks/static blocks/block32x64";
import { block64x32 } from "./blocks/static blocks/block64x32";
import { baseAttack } from "../engine/hitboxes/baseAttack/baseAttack";
import { threeHitNormal } from "../code/actions/attacks/player/threeHitNormal";
import { IBaseAttack } from "../engine/hitboxes/baseAttack/IBaseAttack";
import { baseAttackNull } from "../engine/hitboxes/baseAttack/baseAttackNull";
import { movementDirection } from "../engine/action/attackDirections";
import { slideKick } from "../code/actions/attacks/player/slideKick";
import { forwardAir } from "../code/actions/attacks/player/forwardAir";
import { downAir } from "../code/actions/attacks/player/downAir";
import { neutralAir } from "../code/actions/attacks/player/neutralAir";
import { characterMoves } from "../code/characterMoves";
import { grassFilter } from "../code/filters/groundFilters/grassFilter";
import { dummySandbag } from "./dummySandbag";
import { demonAi1 } from "./demons/demonAi1";
import { objectFunctions } from "../engine/objectHandlers/objectFunctions";
import { upAir } from "../code/actions/attacks/player/upAir";


export class player extends objectBase{
    static objectName = "player";
    airFriction: number = 0.9947;
    friction = 0.7;
    gravity: iVector = new vectorFixedDelta(calculations.degreesToRadians(270), 0);//vector.fromAngleAndMagnitude(calculations.degreesToRadians(270), 0.6);
    weight:number =  0.05;

    sameLayerCollisionOnly = true;
    
    currentSprite: string = "warriorIdle";
    currentSpriteObj: spriteContainer = new spriteContainer();

    collidesWithPolygonGeometry = true;
    public facingRight = true;
    private characterMoveBase;
    private attackTargets = [dummySandbag.objectName, demonAi1.objectName];

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, player.objectName);
        super.setCollision(0, 0, 64, 98);
        //console.log(input);
        
        
        
        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0xFF3e50);
            newGraphics.drawRect(0, 0, 64, 98);
            newGraphics.endFill();
            g.addChild(newGraphics);

            g.calculateBounds();

            return g;
        });

        this.g.filters = [];
        

        super.addCollisionTarget(demonAi1.objectName, block.objectName, block32x64.objectName, block64x32.objectName, movingBlockHori.objectName, movingBlockVert.objectName, "dummySandbag", tinyBlock32.objectName, wideBlock.objectName);

        this.updateCurrentSprite();


        this.characterMoveBase = new characterMoves(ladder.objectName, (l: objectFunctions) => {
            //Ground attacks
            if(l.checkKeyHeld("s")){
                return new slideKick(this, (this.facingRight) ? movementDirection.right : movementDirection.left, this.attackTargets);
            }else{
                return new threeHitNormal(this, (this.facingRight) ? movementDirection.right : movementDirection.left, this.attackTargets);
            }
        }, (l: objectFunctions) => {
            //air attacks
            if(l.checkKeyHeld("d") || l.checkKeyHeld("a")){
                return new forwardAir(this, (this.facingRight) ? movementDirection.right : movementDirection.left, this.attackTargets);
            }else if(l.checkKeyHeld("s")){
                return new downAir(this, (this.facingRight) ? movementDirection.right : movementDirection.left, this.attackTargets);
            }else if(l.checkKeyHeld("w")){
                return new upAir(this, (this.facingRight) ? movementDirection.right : movementDirection.left, this.attackTargets);
            }else if(!l.checkKeyHeld("d") && !l.checkKeyHeld("d") && !l.checkKeyHeld("d") &&!l.checkKeyHeld("d")){
                return new neutralAir(this, (this.facingRight) ? movementDirection.right : movementDirection.left, this.attackTargets);
            }

            return new baseAttackNull();
        });

    }

    init(roomEvents: objectFunctions){
        grassFilter.primaryCollider = this;
        if(roomEvents.getRoomStartString().indexOf("target_room") != -1){
            let targetJson = roomEvents.getRoomStartString()[roomEvents.getRoomStartString().indexOf("target_room") +1];
            let roomStartObj = JSON.parse(targetJson);
            if(roomStartObj.from != null){
                let from: string = roomStartObj.from;
                
                let roomChangers = roomEvents.getSpecificObjects("roomChanger");
                roomChangers.forEach(roomChanger => {
                    let roomChangerJson = JSON.parse(roomChanger.outputString);
                    let facing: string = roomChangerJson.facing;
                    if(roomChangerJson.to == from){
                        this.g.y = roomChanger.g.y;
                        this.g.x = roomChanger.g.x + this.collisionBox.x + this.collisionBox.width/2;
                        if(facing == "right"){
                            this.g.x += 50;
                        }else if(facing == "left"){
                            this.g.x -= 50;
                        }
                    }
                });
            }
        }

    }
    


    logic(l: objectFunctions){
        super.logic(l);
        //l.queryKey();
        if(l.flags().has("freezeObjects")){
            this.characterMoveBase.move(l, this);
        }
        
        l.setCameraTarget(this.g.x + this.collisionBox.x + (this.collisionBox.width/2), this.g.y);
        
        if(this.verticalCollision > 0){
            if(this.force.Dx > 0){
                if(l.checkKeyHeld("d") && l.checkKeyHeld("a") == false){
                    this.facingRight = true;
                }
            }else if(this.force.Dx < 0){
                if(l.checkKeyHeld("a") && l.checkKeyHeld("d") == false){
                    this.facingRight = false;
                }
            }
        }
        

    }



    private playFootstepSounds(){
        resourcesHand.playRandomAudio(["footstepGrass1.wav", "footstepGrass2.wav", "footstepGrass3.wav", "footstepGrass4.wav"]);
    }


    private updateCurrentSprite(){
        super.removeAllSprites();
        if(this.currentSprite != null){
            this.currentSpriteObj.set(super.addSprite(new animConfig({
                animationName: this.currentSprite,
                scaleX: 4,
                scaleY: 4,
                speed: 0.3,
                x: 64,
                y: 77,
                anchorX: 0.5,
                anchorY: 0.34,
            })));
    
            
            if(this.currentSpriteObj != null){
                this.currentSpriteObj.update(x => x.filters = []);
                if(this.facingRight == true){
                    this.currentSpriteObj.update(x => x.pivot.set(0, 15));
                }else{
                    this.currentSpriteObj.update(x => x.pivot.set(-15, 15));
                }
            }   
        }
        
        this.g.calculateBounds();
    }

}