import { objectBase } from "../engine/objectHandlers/objectBase";
import * as PIXI from 'pixi.js'
import { block } from "./blocks/static blocks/block";
import { movingBlockHori } from "./blocks/movingBlockHori";
import { movingBlockVert } from "./blocks/movingBlockVert";
import { iVector } from "../engine/dataObjects/vector/iVector";
import { vectorFixedDelta } from "../engine/dataObjects/vector/vectorFixedDelta";
import { calculations } from "../engine/calculations";
import { roomEvent } from "../engine/roomEvent/roomEvent";
import { animConfig } from "../engine/objectHandlers/animConfig";
import { dummySandbag } from "./dummySandbag";
import { hitbox } from "../engine/hitboxes/hitbox/hitbox";
import { player } from "./player";
import { tinyBlock32 } from "./blocks/tinyBlock32";
import { wideBlock } from "./blocks/wideBlock";
import { spriteContainer } from "../engine/dataObjects/spriteContainer";
import { block32x64 } from "./blocks/static blocks/block32x64";
import { block64x32 } from "./blocks/static blocks/block64x32";
import { objectFunctions } from "../engine/objectHandlers/objectFunctions";




export class mio extends objectBase{
    static objectName = "mio";
    airFriction: number = 0.93;
    gravity: iVector = new vectorFixedDelta(calculations.degreesToRadians(270), 0);//vector.fromAngleAndMagnitude(calculations.degreesToRadians(270), 0.6);
    weight:number =  0.09;

    maxRunSpeed = 13;
    normalRunSpeed = 13;
    superRunSpeed = 19;

    currentSprite: string = "catReady";
    currentSpriteObj: spriteContainer = new spriteContainer();

    jumpAngle: number = 0;
    
    private shakeCameraForce = 0;
    private airbornTimer = 0;
    public facingRight = true;

    private climbing = false;
    private canClimb = true;
    private falling = false;
    private climbingTimer = 0;
    private hasJumped = false;

    private constantForce = 0;
    private attacking = false;

    private actionWait = 0;

    private hitboxToCreate: [number, hitbox] | null = null;

    private jumpsquateLength = 4;
    private jumpSquatCounter = -1;

    private myShaderFrag = `
    varying vec2 vTextureCoord;
    uniform sampler2D uSampler;


    float rand(vec2 co){
        return fract(sin(dot(co, vec2(12.9898, 78.233))) * 43758.5453);
    }


    void main(void)
    {
        float width = 256.0;
        float height = 175.0;

        int rings = int(width/16.0);

        vec4 t = texture2D(uSampler, vTextureCoord);

        int color = 1;
        int spot = 0;

        float distanceFromCenterX = vTextureCoord.x - 0.5;
        float distanceFromCenterY = vTextureCoord.y - (1.0 - 0.68/*height/width*/);
        float hyputenuse = sqrt((distanceFromCenterX*distanceFromCenterX) + (distanceFromCenterY*distanceFromCenterY));

        /*if(floor(hyputenuse*100.0) == 15.0){
            color = 1;
        }*/

        float floatPerPixel = 1.0/width;

        float random = rand(vTextureCoord);

        float averageColorStrength = (t.x + t.y + t.z)/3.0;
        



        if(averageColorStrength > 0.8){ //face and part of sword
            color = -1;
        }

        if(averageColorStrength < 0.8
            && averageColorStrength > 0.4){//armor
                color = 2;
        }

        if(averageColorStrength < 0.3
            && averageColorStrength > 0.2){//hair and part of wrists
                color = 3;
        }



        for(int i=0; i<128; i++){

            int draw = int(float(i) - (2.0 * floor(float(i)/2.0)));
            if(draw == 1 && i > 3 + int((128.0*random)) ){
                if(hyputenuse < float(i)*floatPerPixel && hyputenuse > float(i-int(32.0*averageColorStrength))*(floatPerPixel)){
                    spot = 1;
                }
            }
            
        }


        int colorIt = 0;
        if(random > 0.5){
            //color = 1;
            //colorIt = 1;
        }

        
        //t.a = 1.0;

        vec4 colorOut = vec4(1.0 * t.a, 1.0 * t.a, 1.0 * t.a, t.a);

        if(color == -1){//white
            colorOut = vec4(1.0 * t.a, 1.0 * t.a, 1.0 * t.a, t.a);
        }else if(color == 0){//black
            colorOut = vec4(0.0 * t.a, 0.0 * t.a, 0.0 * t.a, t.a);
        }else if(color == 1){//blue
            colorOut = vec4(0.0 * t.a, 0.0 * t.a, vTextureCoord.y * t.a, t.a);
        }else if(color == 2){//silver
            colorOut = vec4(0.4 * t.a, 0.4 * t.a, 0.4 * t.a, t.a);
        }else if(color == 3){//yellow
            colorOut = vec4(0.7 * t.a, 0.7 * t.a, 0.09 * t.a, t.a);
        }else{
            colorOut = vec4(0.0 * t.a, 0.0 * t.a, 0.0 * t.a, t.a);
        }

        if(spot == 1){
            colorOut = colorOut*colorOut;
        }

        gl_FragColor = colorOut;

        
    }
    `;


    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, mio.objectName);
        super.setCollision(14, 40, 100, 88);


        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0xFF3e50);
            newGraphics.drawRect(14, 40, 100, 88);
            newGraphics.endFill();
            g.addChild(newGraphics);

            g.calculateBounds();

            return g;
        });



        super.addCollisionTarget(block.objectName, block32x64.objectName, block64x32.objectName, movingBlockHori.objectName, movingBlockVert.objectName, 
            dummySandbag.objectName, tinyBlock32.objectName, wideBlock.objectName);

        this.updateCurrentSprite();

    }


    


    logic(l: objectFunctions){
        super.logic(l);
        
        if(Math.floor(this.force.Dy) == 0&& Math.floor(this.gravity.magnitude) == 0){
            this.hasJumped = false;
        }

        if(this.climbing == false){
            
            if((l.checkKeyHeld("a") || l.checkKeyHeld("d")) && this.force.Dx == 0 && Math.floor(this.force.Dy) > 0 && this.canClimb && this.falling == false && this.hasJumped){
                this.climbing = true;
                this.gravity.magnitude = 0;
                this.climbingTimer = 70;
                this.canClimb = false;
            }
        }else{
            if(this.force.Dx != 0 || this.climbingTimer <= 0 ||(l.checkKeyHeld("a") || l.checkKeyHeld("d")) == false || (l.checkKeyHeld("a") && l.checkKeyHeld("d"))){
                this.climbing = false;
                this.weight = 0.09;
                this.falling = true;
            }else{
                this.falling = false;
                this.airbornTimer = 0;
                this.weight = 0.001;
            }
            if(this.climbingTimer > 0){
                this.climbingTimer --;
            }
        }
        
        
        if(Math.floor(this.force.Dy) == 0 && Math.round(this.gravity.magnitude) == 0){
            this.canClimb = true;
            this.falling = false;
        }
        
        if(l.checkKeyHeld("Control")){
            this.maxRunSpeed = this.superRunSpeed;
        }else{
            this.maxRunSpeed = this.normalRunSpeed;
        }
        
        if(l.checkKeyHeld("a") && this.actionWait == 0){
            super.addForceAngleMagnitude(calculations.degreesToRadians(180), (1/13)*this.maxRunSpeed);
        }
        if(l.checkKeyHeld("d")  && this.actionWait == 0){
            super.addForceAngleMagnitude(calculations.degreesToRadians(0), (1/13)*this.maxRunSpeed);
        }

        if(this.climbing == false){
            if(this.falling == false && this.hasJumped == false && l.checkKeyPressed("w") && Math.floor(this.gravity.magnitude) == 0 && this.actionWait == 0){
                super.addForceAngleMagnitude(calculations.degreesToRadians(90), 26);
                this.hasJumped = true;
            }
        }else{
            
            if(l.checkKeyHeld("w")){
                super.addForceAngleMagnitude(calculations.degreesToRadians(90), 0.5);
            }
        }
        
        

        this.force.limitHorizontalMagnitude(this.maxRunSpeed);
        
        

        if(this.climbing && this.force.Dy < 0){
            this.currentSprite = "catRun";
        }else
        if(this.force.Dy <= -9 && this.gravity.magnitude > 0){
            this.currentSprite = "catJumpUp";
        }else if(this.force.Dy > -9  && this.force.Dy < 9 && this.gravity.magnitude > 0){
            this.currentSprite = "catJumpMid";
        }else if(this.force.Dy > 9 && this.gravity.magnitude > 0){
            this.currentSprite = "catJumpDown";
        }


        if(Math.abs(this.force.Dy) < 1){
            this.jumpAngle = 0;
            if(Math.abs(this.force.Dx) <= 1){
                this.currentSprite = "catReady";
            }else if(this.force.Dx != 0){
                this.currentSprite = "catRun";
            }
        }

        if(this.force.Dy > 1){
            if(this.force.Dx > 0 && this.jumpAngle < 0.4){
                this.jumpAngle += 0.015;
            }else if(this.jumpAngle > -0.4){
                this.jumpAngle -= 0.015;
            }
        }
        

        if(super.hasSprite(this.currentSprite) == false){
            super.removeAllSprites();
            this.updateCurrentSprite();
        }

        
        this.currentSpriteObj.update(x => x.rotation = this.jumpAngle);
        if(this.climbing){
            if(l.checkKeyHeld("a")){
                this.currentSpriteObj.update(x => x.rotation = calculations.degreesToRadians(90));
            }else if(l.checkKeyHeld("d")){
                this.currentSpriteObj.update(x => x.rotation = calculations.degreesToRadians(270));
            }
        }
        if(this.currentSprite == "catRun"){
            let animWithSpeed = 0.5*Math.abs(this.force.Dx)/this.superRunSpeed;
            if(animWithSpeed < 0.1) animWithSpeed = 0.1;
            this.currentSpriteObj.update(x => x.animationSpeed = animWithSpeed);
        }
        

        if(Math.abs(Math.floor(this.force.Dx)) != 0){
            if(this.force.Dx > 0){
                super.scaleXSprites(3);
                this.facingRight = true;
                if(this.jumpAngle < 0){
                    this.jumpAngle *= -1;
                }
            }else if(this.force.Dx < 0){
                super.scaleXSprites(-3);
                this.facingRight = false;
                if(this.jumpAngle > 0){
                    this.jumpAngle *= -1;
                }
            }
        }

        if(Math.abs(Math.floor(this.force.Dx)) >= 5){
            l.setCameraMoveSpeedX(0.07);
        }else{
            l.setCameraMoveSpeedX(0.04);
        }



        

        if(this.force.Dy >= 1){
            this.airbornTimer ++;
        }else{
            if(this.airbornTimer > 25){
                this.shakeCameraForce = this.airbornTimer*0.7;
                if(this.shakeCameraForce > 40){
                    this.shakeCameraForce = 40;
                }
            }
            this.airbornTimer = 0;
        }

        this.hangleAttacks(l);

        if(this.shakeCameraForce > 0){
            l.setCameraOffsetX(-this.shakeCameraForce + Math.random()*this.shakeCameraForce);
            l.setCameraOffsetY(-this.shakeCameraForce + Math.random()*this.shakeCameraForce);
            this.shakeCameraForce--;
        }else{
            l.setCameraOffsetX(0);
            l.setCameraOffsetY(0);
        }

        let spaceToAdd = 128;
        if(Math.abs(this.force.Dx) > 2){
            spaceToAdd = 256;
        }
        let addCamSpace = spaceToAdd;
        if(this.facingRight == false){
            addCamSpace = -spaceToAdd;
        }
        let addCamSpaceY = 0;
        if(Math.floor(this.force.Dy) > 30){
            addCamSpaceY = 600;
        }
        l.setCameraTarget(this.g.x + addCamSpace + 64, this.g.y + addCamSpaceY);



        /*if(this.hitboxToCreate != null){
            if(this.hitboxToCreate[0] > 0){
                this.hitboxToCreate[0]--;
                if(this.hitboxToCreate[0] == 0){
                    l.addObject(this.hitboxToCreate[1], this.onLayer);
                }
                
            }

            if(this.hitboxToCreate != null && this.hitboxToCreate[1].aerial && (this.climbing || Math.round(this.gravity.Dy) == 0)){
                this.hitboxToCreate[1].life *= 0.8;
            }
        }*/

        
        if(l.checkKeyReleased("p") && this.attacking == false){
            l.deleteObject(this);
            l.addObject(new player(this.g.x, this.g.y-32, ""), this.onLayer);
        }

    }




    private hangleAttacks(l: objectFunctions){
        if(l.checkKeyPressed(" ") && this.actionWait == 0 && this.climbing == false){
            if(Math.floor(this.gravity.magnitude) != 0){
                this.attacking = true;
                this.constantForce = 15;
                this.actionWait = 33;
                this.airbornTimer += 5;

                /*resourcesHand.playAudioVolume("playerBeastAttack1.ogg", 0.2);
                this.hitboxToCreate = tools.createHitbox({
                    startupTime: 4,
                    x: this.g.x, 
                    y: this.g.y, 
                    creator: this,
                    life: 24,
                    size: [64, 80],
                    offset: [60, 54],
                    hitboxDirection: new vectorFixedDelta(calculations.degreesToRadians(24), 16),
                    aerial: true,
                    type: "playerBeastForm"
                });*/

            }else{
                this.attacking = true;
                this.constantForce = 10;
                this.actionWait = 25;

                /*this.hitboxToCreate = tools.createHitbox({
                    startupTime: 4,
                    x: this.g.x, 
                    y: this.g.y, 
                    creator: this,
                    life: 6,
                    size: [98, 48],
                    offset: [64, 5],
                    hitboxDirection: new vectorFixedDelta(calculations.degreesToRadians(24), 12),
                    aerial: false,
                    type: "playerBeastForm"
                });*/

            }
        }

        if(this.facingRight){
            super.addForceAngleMagnitude(calculations.degreesToRadians(350), this.constantForce);
        }else{
            super.addForceAngleMagnitude(calculations.degreesToRadians(190), this.constantForce);
        }
        
        if(this.constantForce > 0){
            this.constantForce -= 1;
        }else{
            this.attacking = false;
        }

        if(this.actionWait > 0){
            this.actionWait --;
        }
    }




    private updateCurrentSprite(){
        super.removeAllSprites();
        if(this.currentSprite != null){
            this.currentSpriteObj.set(super.addSprite(new animConfig({
                animationName: this.currentSprite,
                scaleX: 3,
                scaleY: 3,
                speed: 0.3,
                x: 64,
                y: 77,
                anchorX: 0.5,
                anchorY: 0.34,
            })))
            if(this.currentSpriteObj != null){
                if(this.currentSprite == "catReady"){
                    this.currentSpriteObj.update(x => x.animationSpeed = 0.155);
                }
                this.currentSpriteObj.update(x => x.pivot.set(0, 25));
            }
        }
        
    }






}